import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        WXUserInfo: {},
        appUserInfo: {},
        distributionInfo: {}
    },
    mutations: {
        setWXUserInfo(state, v) {
            state.WXUserInfo = v
        },
        setAppUserInfo(state, v) {
            state.appUserInfo = v
        },
        setDistributionInfo(state, v) {
            state.distributionInfo = v
        },
    },
    actions: {},
    getters: {},
    plugins: [createPersistedState()],
})