const routes = [
    {
        path: '/',
        name: 'home',
        component: () => import('../home/home.vue'),
        redirect: "/detail",
        children: [
            {
                path: '/detail',
                name: 'detail',
                component: () => import('../views/detail.vue'),
                meta: {
                    isOutSide: true,
                    title: '分销'
                }

            }, {
                path: '/detail1',
                name: 'detail1',
                component: () => import('../views/detail1.vue'),
                meta: {
                    isOutSide: true
                }

            },
            {
                path: '/login',
                name: 'login',
                component: () => import('../views/login.vue'),
                meta: {
                    isOutSide: false,
                    title: "登录"
                }
            },
            {
                path: '/personalCenter',
                name: 'personalCenter',
                component: () => import('../views/personalCenter.vue'),
                meta: {
                    isOutSide: false,
                    title: '个人中心'
                }
            },
            {
                path: '/orderRecord',
                name: 'orderRecord',
                component: () => import('../views/orderRecord.vue'),
                meta: {
                    isOutSide: false,
                    title: "订单记录"
                }
            },
            {
                path: '/distributionRecord',
                name: 'distributionRecord',
                component: () => import('../views/distributionRecord.vue'),
                meta: {
                    isOutSide: false,
                    title: "分销记录"
                }
            },
            {
                path: '/userWallet',
                name: 'userWallet',
                component: () => import('../views/userWallet.vue'),
                meta: {
                    isOutSide: false,
                    title: '钱包'
                }
            },
            {
                path: '/accountDetail',
                name: 'accountDetail',
                component: () => import('../views/accountDetail.vue'),
                meta: {
                    isOutSide: false,
                    title: '收支明细'
                }
            },
            {
                path: '/withDrawal',
                name: 'withDrawal',
                component: () => import('../views/withDrawal.vue'),
                meta: {
                    isOutSide: false,
                    title: "提现申请"
                }
            },
            {
                path: '/share',
                name: 'share',
                component: () => import('../views/share.vue'),
                meta: {
                    isOutSide: false,
                    title: "分享"
                }
            }
        ]
    },
]

export default routes
