import request from './request'

//获取微信授权
export const getWX = (data) => {
    return request({
        method: 'post',
        data,
        url: '/socials/weixin/authorizations',
        token: false
    })
}

//获取详情页数据
export const getDetailData = (params) => {
    return request({
        method: 'get',
        params,
        url: '/activity/detail/' + params.ac_id,
        token: false
    })
}

//获取登录手机验证码
export const getPhoneCode = (data) => {
    return request({
        method: 'post',
        data,
        url: '/verificationCodes',
        token: false
    })
}

