import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
import store from '../store/index'
import { getWX } from "../assets/javascript/apis.js";

Vue.use(VueRouter)

//解决使用this.$router.push,跳转向同一个页面会发生报错的问题
//同时能够解决使用this.$router.push,跳转时发生重定向报错的问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
  mode: 'hash',
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    };
  }
})


//验证token的函数
let testToken = () => {
  if (store.state.appUserInfo.token) {
    let setTime = new Date(store.state.appUserInfo.expired_at)
    setTime = setTime.getTime()
    let nowTime = new Date().getTime()
    if (nowTime > setTime) {
      return false
    } else {
      return true
    }
  } else {
    return false
  }
}

router.beforeEach((to, from, next) => {
  console.log(to)
  // let url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx4c0abf64574082b8&redirect_uri=http://192.168.10.25:8081/' + to.name + '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
  console.log(window.location.href)
  let url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx154aec27e3c4534a&redirect_uri=https%3a%2f%2fh5.newtiming.com&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'

  /* https://open.weixin.qq.com/connect/oauth2/authorize?
  appid=wx154aec27e3c4534a
  &redirect_uri=https://h5.newtiming.com/
  &connect_redirect=1&uin=MzgwNzI4MjMy&key=0294f686ee695bc9894990d64099de27137a4c75846c694fc55ff8fd20cc598d64e6ab24674aacfa8cca2f0e210fc894&pass_ticket=+PK8l1Jp2jlCQzULX+3QS0sLFfWuU/iHlOSwIRlfBhXHJlqRMBD+VePCM84LW98ht7Lk0II2+vAu7wbx705baQ==
*/
  // https://h5.newtiming.com
  // debugger
  // next()
  // console.log(store.state)
  // testToken()
  if (!to.meta.isOutSide) {
    if (store.state.WXUserInfo.id) {
      if (to.name == "login") {
        next()
      } else {
        if (testToken()) {
          next()
        } else {
          next('/login')
        }
      }
    } else if (to.query.code) {
      getWX({
        code: to.query.code
      }).then(res => {
        console.log(res)
        store.commit("setWXUserInfo", res)
        if (to.name == "login") {
          next()
        } else {
          if (testToken()) {
            next('/detail')
          } else {
            next('/login')
          }
        }
      })
    } else {
      location.replace(url);
    }
  } else {
    next()
  }
})

router.afterEach((to, from) => {
  document.title = to.meta.title
})

export default router
